h1 {
  font-family: "PlayfairDisplay-Bold";
  font-size: 3em;
  margin: 0;
  padding: 0;
  text-align: center;
  letter-spacing: 3px;
}

h2,
h3 {
  font-family: "PlayfairDisplay-Bold";
  font-size: 25px;
  margin: 0;
  padding: 0;
  text-align: center;
}

h3 {
  font-size: 20px;
  margin-top: 20px;
}

form {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  border: 2px solid;
  border-radius: 15px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

 input[type="radio"] {
  margin-top: 15px;
  opacity: 0;
}

label {
  position: relative;
  display: inline-block;
  padding-left: 22px;
}

label::before,
label::after {
  position: absolute;
  content: "";
  display: inline-block;
}

label::before{
  height: 16px;
  width: 16px;
  border: 1px solid;
  border-radius: 2px;
  left: -5px;
  top: 0px;
}

label::after {
  height: 5px;
  width: 9px;
  border-left: 2px solid;
  border-bottom: 2px solid;
  transform: rotate(-45deg);
  left: 0px;
  top: 4px;
}

input[type="radio"] + label::after {
  content: none;
}

input[type="radio"]:checked + label::after {
  content: "";
}

input[type="radio"]:focus + label::before {
  outline: rgb(59, 153, 252) auto 5px;
}
