.colorsSelector {
  display: flex;
  justify-content: center;

}

.colorItem {
  display: block;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin: 10px;
}

.colorWhite {
  background-color: white;
  border: 1px solid rgba(44, 42, 42, 0.308);
}

.colorBlue {
  background-color: #246fb7;
}

.colorRed {
  background-color: #cf1d20;
}

.colorOrange {
  background-color: #c47132;
}

.colorGrey {
  background-color:#8a8c8e;
}

.colorSilver {
  background-color: #d8dcdf;
}

.colorBlack {
  background-color: #000000;
}
