
.imgHolder {
  display: flex;
  justify-content: center;
  margin-top: 5%;

  .model-name {
    font-family: "PlayfairDisplay-Bold";
    font-size: 25px;
    letter-spacing: 2px;
    margin-top: 5px;
  }

  .model-specs {
    margin-top: 15px;
    font-size: 17px;
    line-height: 22px;
    margin-top: 10px;
  }

  .model-price {
    font-size: 20px;
    font-weight: bold;
    line-height: 22px;
    margin-top: 10px;
  }

  .model-summary {
    display: flex;
    flex-direction: column;
    margin: 0;
    margin-left: 20px;
    padding: 0;
    text-align: center;
    height: 180px;
    max-width: 230px;
  }

  .button {
    display: block;
    cursor: pointer;
    margin-top: 15px;
    height: 40px;
    width: auto;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-family: "PlayfairDisplay-Regular";
    font-weight: bold;
    background-color: transparent;
    border: 1px solid rgb(63, 62, 49);
    &:hover {
      background-color: rgb(242, 252, 253);
    }
  }
}
