.container {
  min-width: 320px;
  padding: 0 50px;
  margin: 0 auto;
}
.colorsSelector {
  display: flex;
  justify-content: center;
}
.colorItem {
  display: block;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin: 10px;
}
.colorWhite {
  background-color: white;
  border: 1px solid rgba(44, 42, 42, 0.308);
}
.colorBlue {
  background-color: #246fb7;
}
.colorRed {
  background-color: #cf1d20;
}
.colorOrange {
  background-color: #c47132;
}
.colorGrey {
  background-color: #8a8c8e;
}
.colorSilver {
  background-color: #d8dcdf;
}
.colorBlack {
  background-color: #000000;
}
@font-face {
  font-family: "PlayfairDisplay-Bold";
  src: url(../fonts/PlayfairDisplay-Bold.woff2) format("woff2"), url(../fonts/PlayfairDisplay-Bold.woff) format("woff");
}
@font-face {
  font-family: "PlayfairDisplay-Regular";
  src: url(../fonts/PlayfairDisplay-Regular.woff2) format("woff2"), url(../fonts/PlayfairDisplay-Regular.woff) format("woff");
}
.imgHolder {
  display: flex;
  justify-content: center;
  margin-top: 5%;
}
.imgHolder .model-name {
  font-family: "PlayfairDisplay-Bold";
  font-size: 25px;
  letter-spacing: 2px;
  margin-top: 5px;
}
.imgHolder .model-specs {
  margin-top: 15px;
  font-size: 17px;
  line-height: 22px;
  margin-top: 10px;
}
.imgHolder .model-price {
  font-size: 20px;
  font-weight: bold;
  line-height: 22px;
  margin-top: 10px;
}
.imgHolder .model-summary {
  display: flex;
  flex-direction: column;
  margin: 0;
  margin-left: 20px;
  padding: 0;
  text-align: center;
  height: 180px;
  max-width: 230px;
}
.imgHolder .button {
  display: block;
  cursor: pointer;
  margin-top: 15px;
  height: 40px;
  width: auto;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-family: "PlayfairDisplay-Regular";
  font-weight: bold;
  background-color: transparent;
  border: 1px solid #3f3e31;
}
.imgHolder .button:hover {
  background-color: #f2fcfd;
}
h1 {
  font-family: "PlayfairDisplay-Bold";
  font-size: 3em;
  margin: 0;
  padding: 0;
  text-align: center;
  letter-spacing: 3px;
}
h2,
h3 {
  font-family: "PlayfairDisplay-Bold";
  font-size: 25px;
  margin: 0;
  padding: 0;
  text-align: center;
}
h3 {
  font-size: 20px;
  margin-top: 20px;
}
form {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  border: 2px solid;
  border-radius: 15px;
}
.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
input[type="radio"] {
  margin-top: 15px;
  opacity: 0;
}
label {
  position: relative;
  display: inline-block;
  padding-left: 22px;
}
label::before,
label::after {
  position: absolute;
  content: "";
  display: inline-block;
}
label::before {
  height: 16px;
  width: 16px;
  border: 1px solid;
  border-radius: 2px;
  left: -5px;
  top: 0px;
}
label::after {
  height: 5px;
  width: 9px;
  border-left: 2px solid;
  border-bottom: 2px solid;
  transform: rotate(-45deg);
  left: 0px;
  top: 4px;
}
input[type="radio"] + label::after {
  content: none;
}
input[type="radio"]:checked + label::after {
  content: "";
}
input[type="radio"]:focus + label::before {
  outline: #3b99fc auto 5px;
}
